import PropTypes from 'prop-types';
import React from 'react';
import { Redirect, useLocation } from 'react-router-dom';
import { MdContactPhone } from 'react-icons/md';

import { __t } from '@mssgme/shared';
import { CreditCardIcon, MapPinIcon, ShoppingBagIcon } from '@mssgme/icons';
import { Spinner } from '@mssgme/ui';
import { useCoreContext, useCRMData } from '../../hooks';
import { Step, StepSwitch, ThemedModal } from '../../UI';
import { useOrder } from '../useOrder';
import AdjustingStep from './Steps/AdjustingStep';
import ShippingAddressStep from './Steps/ShippingAddressStep';
import PaymentStep from './Steps/PaymentStep';
import ContactInfoStep from './Steps/ContactInfoStep';

import styles from './OrderPage.scss';

OrderPage.propTypes = {
    orderId: PropTypes.string.isRequired,
};

export function OrderPage({ orderId }) {
    const location = useLocation();
    const { router } = useCoreContext();
    const { form } = useCRMData();
    const { immediate: order, loading, error, isNotFound } = useOrder(orderId);
    const backPath = location.state?.backPath || router.toHome();

    if (!order) {
        return null;
    }

    if (order.finalized) {
        return <Redirect to={router.orders.toStatus(orderId, order.status)} />;
    }

    return (
        <ThemedModal overlay compact title={__t('checkout')} onClose={backPath}>
            <div className={styles.root}>
                {loading ? (
                    <Spinner />
                ) : error ? (
                    <div className={styles.error}>
                        {isNotFound ? __t('order_not_found') : error.message}
                    </div>
                ) : (
                    <StepSwitch>
                        <Step name={__t('order_adjusting')} icon={ShoppingBagIcon}>
                            <AdjustingStep />
                        </Step>
                        <Step name={__t('order_contact_info')} icon={MdContactPhone}>
                            <ContactInfoStep />
                        </Step>
                        {!!form.collectAddress && (
                            <Step name={__t('order_shipping_address')} icon={MapPinIcon}>
                                <ShippingAddressStep />
                            </Step>
                        )}
                        <Step name={__t('order_payment')} icon={CreditCardIcon}>
                            <PaymentStep />
                        </Step>
                    </StepSwitch>
                )}
            </div>
        </ThemedModal>
    );
}
