import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useAsyncFn, useToggle } from 'react-use';
import { AnimatePresence, motion } from 'framer-motion';

import { __t } from '@mssgme/shared';
import { useBooleanHandler, useNavigationUtils } from '@mssgme/hooks';
import {
    handlesPaymentsStatus,
    useContacts,
    useFrontAPI,
    useModalThemes,
    usePaymentConfigs,
    useRenderer,
} from '../../hooks';
import { ThemedButton, ThemedModal } from '../../UI';
import { useOrder } from '../useOrder';
import { PAYMENT_PROVIDER_RENDERERS } from '../OrderPage';
import { Section } from './Section';

import styles from './UserOrderPage.scss';

UserOrderPage.propTypes = {
    orderId: PropTypes.string.isRequired,
};

export function UserOrderPage({ orderId }) {
    const api = useFrontAPI();
    const payments = usePaymentConfigs();
    const renderBlock = useRenderer();
    const contacts = useContacts();
    const [isPayNowModal, togglePayNowModal] = useToggle(false);
    const [isShowingCancelPrompt, showCancelPrompt, hideCancelPrompt] = useBooleanHandler();
    const { generateUrl } = useNavigationUtils();
    const { contentStyle } = useModalThemes();
    const { reload, order, loading: orderLoading, error, isNotFound } = useOrder(orderId);

    const { immediate, online } = useMemo(() => {
        const mapper = ([provider]) => [provider, PAYMENT_PROVIDER_RENDERERS[provider]];

        return {
            immediate: payments.immediate.map(mapper),
            online: payments.online.map(mapper),
        };
    }, [payments]);
    const [{ loading: finalizeLoading }, handlePay] = useAsyncFn(
        async (paymentMethod) => {
            await api.orders.finalize({ orderId, payload: { paymentMethod } });
            togglePayNowModal();
            reload();
        },
        [orderId]
    );
    const [{ loading: cancelLoading }, handleCancelOrder] = useAsyncFn(async () => {
        await api.orders.cancel({ orderId });
        hideCancelPrompt();
        reload();
    }, [orderId]);

    if (!order || !order.finalized) {
        return null;
    }

    const loading = finalizeLoading || orderLoading || cancelLoading;
    const isJustOneOnlinePaymentMethod = !immediate.length && online.length === 1;
    const onlineElements = online.map(
        ([key, Renderer]) => <Renderer key={key} order={order} disabled={loading} onPay={handlePay} />
    );

    return (
        <ThemedModal
            overlay
            compact
            padding
            className={styles.root}
            title={`${__t('order_term')} #${order.feedback.order}`}
            onClose={generateUrl('..')}
        >
            {error ? (
                <div>{isNotFound ? __t('order_not_found') : error.message}</div>
            ) : (
                <div>
                    <Section>
                        <div>
                            <div>{__t('order_status')}</div>
                            <div>{__t(`order_status_${order.feedback.status}`)}</div>
                        </div>
                        {handlesPaymentsStatus(order.paymentMethod) && (
                            <div>
                                <div>{__t('payment_status')}</div>
                                <div>{__t(`payment_status_${order.status}`)}</div>
                            </div>
                        )}
                    </Section>

                    <Section title={__t('your_contacts')}>
                        <div>
                            <div>{__t('name')}</div>
                            <div>
                                {order.contactInfo.lastName} {order.contactInfo.firstName}
                            </div>
                        </div>
                        <div>
                            <div>{__t('phone')}</div>
                            <div>{order.contactInfo.phoneNumber}</div>
                        </div>
                        {order.shippingAddress && (
                            <div>
                                <div>{__t('shipping')}</div>
                                <div>
                                    {[
                                        order.shippingAddress.city,
                                        order.shippingAddress.state,
                                        order.shippingAddress.address,
                                        order.shippingAddress.postalCode,
                                    ]
                                        .filter(Boolean)
                                        .join(', ')}
                                </div>
                            </div>
                        )}
                        {order.contactInfo.comment && (
                            <div>
                                <div>{__t('comment')}</div>
                                <div>{order.contactInfo.comment}</div>
                            </div>
                        )}
                    </Section>
                    <Section className={styles.products} title={__t('order_content')}>
                        {order.items.map((product, index) => (
                            <div key={index}>
                                <div>{product.name}</div>
                                <div>
                                    {product.quantity}x{product.price.amount} {product.price.currency}
                                </div>
                            </div>
                        ))}
                        <div>
                            <div>{__t('order_summary')}</div>
                            <div>
                                {order.total.amount} {order.total.currency}
                            </div>
                        </div>
                    </Section>

                    {contacts && (
                        <Section className={styles.messengers} title={__t('store_contacts')}>
                            {contacts.map(renderBlock)}
                        </Section>
                    )}

                    <AnimatePresence>
                        {order.status === 'failed' && order.feedback.status !== 'rejected' && (
                            <motion.div
                                key="order-actions"
                                className={styles.buttons}
                                exit={{ opacity: 0, height: 0, marginTop: '0px' }}
                            >
                                {isJustOneOnlinePaymentMethod ? (
                                    onlineElements
                                ) : (
                                    <ThemedButton theme={contentStyle} onClick={togglePayNowModal}>
                                        {__t('pay_order')}
                                    </ThemedButton>
                                )}

                                <ThemedButton theme={contentStyle} contour onClick={showCancelPrompt}>
                                    {__t('cancel_order')}
                                </ThemedButton>
                            </motion.div>
                        )}

                        {!isJustOneOnlinePaymentMethod && isPayNowModal && (
                            <ThemedModal
                                key="online-payment-options"
                                overlay
                                compact
                                padding
                                className={styles.paymentButtons}
                                title={__t('choose_payment_method')}
                                onClose={togglePayNowModal}
                            >
                                {onlineElements}
                            </ThemedModal>
                        )}

                        {isShowingCancelPrompt && (
                            <ThemedModal
                                key="order-cancellation"
                                overlay
                                compact
                                padding
                                title={__t('order_cancellation')}
                                onClose={hideCancelPrompt}
                            >
                                <div className={styles.cancelPrompt}>
                                    {__t('order_cancellation_prompt')}
                                </div>

                                <ThemedButton
                                    theme={contentStyle}
                                    onClick={handleCancelOrder}
                                    disabled={loading}
                                >
                                    {__t('order_cancellation_prompt_button')}
                                </ThemedButton>
                            </ThemedModal>
                        )}
                    </AnimatePresence>
                </div>
            )}
        </ThemedModal>
    );
}
